.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-bottom: -100%;
  padding: 30px 0;
  z-index: 2;

  @include vp-1199 {
    padding: 40px 0;
  }

  @include vp-767 {
    padding: 20px 0;
  }
}

.header__contact a {
  font-family: $ff-alumni;
  font-size: 600;
  font-size: 26px;
  line-height: 31px;
  color: $color-default-white;

  @include vp-1199 {
    font-size: 24px;
    line-height: 30px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 24px;
  }
}
