  .tabs__controls {
    width: 322px;
  }
}
@media (max-width: 767px) {
  .tabs__controls {
    width: 290px;
  }
}

.tabs__control {
  position: relative;
  cursor: pointer;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: #102654;
  opacity: 0.5;
  min-width: auto;
  justify-content: center;
  border: none;
  background-color: transparent;
  padding: 0;
}
@media (max-width: 1199px) {
  .tabs__control {
    font-size: 18px;
    line-height: 27px;
  }
}
@media (max-width: 767px) {
  .tabs__control {
    font-size: 16px;
    line-height: 24px;
  }
}
.tabs__control.is-active {
  opacity: 1;
  pointer-events: none;
}
.tabs__control.is-active::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  left: 0;
  bottom: -2px;
  background-color: #d10a42;
}

.tabs__content {
  transition: height 0.3s;
}

.tabs__element {
  width: 100%;
}
@media screen and (max-width: 767px) {
  .tabs__element {
    padding: 15px;
  }
}
.tabs__element p {
  margin: 0;
}

.tabs--unset .tabs__content {
  height: 100px;
}

.tabs--unset .tabs__element {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

[data-tabs=parent] {
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

